import { getReviewsByProductId, createReview, getProductById } from './api.js';

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ –æ–¥–Ω–æ–≥–æ –æ—Ç–∑—ã–≤–∞
const createReviewItemComponent = ({ name = '–ì–æ—Å—Ç—å', rating = 0, text = '–û—Ç–∑—ã–≤ –æ—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç' }) => {
    const reviewItem = document.createElement('li');
    reviewItem.className = 'review-item';

    reviewItem.innerHTML = `
        <ul>
            <li class="review-author"><h5>${name}</h5></li>
            <li class="review-rating"><h5>‚òÖ ${rating}</h5></li>
            <li class="review-text">${text}</li>
        </ul>
    `;

    return reviewItem;
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Å–µ–∫—Ü–∏–∏ —Å–ø–∏—Å–∫–∞ –æ—Ç–∑—ã–≤–æ–≤
const createReviewsListComponent = (reviews, productTitle) => {
    const reviewsList = document.createElement('ul');
    reviewsList.className = 'reviews-list';

    if (reviews.length === 0) {
        const noReviewsItem = document.createElement('li');
        noReviewsItem.className = 'no-reviews-item';
        noReviewsItem.innerHTML = `<h5 style="color: #711010">–û—Ç–∑—ã–≤–æ–≤ –Ω–∞ –¥–∞–Ω–Ω—ã–π —Ç–æ–≤–∞—Ä –ø–æ–∫–∞ –Ω–µ—Ç, –Ω–æ –≤—ã –º–æ–∂–µ—Ç–µ —Å—Ç–∞—Ç—å –ø–µ—Ä–≤—ã–º, –∫—Ç–æ –ø–æ–¥–µ–ª–∏—Ç—Å—è —Å–≤–æ–∏–º–∏ –≤–ø–µ—á–∞—Ç–ª–µ–Ω–∏—è–º–∏ –æ "${productTitle}"</h5>`;
        reviewsList.appendChild(noReviewsItem);
    } else {
        reviews.forEach(review => {
            const reviewItem = createReviewItemComponent(review);
            reviewsList.appendChild(reviewItem);
        });
    }

    return reviewsList;
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –æ—Ç–∑—ã–≤–∞ —á–µ—Ä–µ–∑ API
const sendReview = async (reviewData) => {
    try {
        const response = await createReview(reviewData);
        return response;
    } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –æ—Ç–∑—ã–≤–∞:', error);
        throw error;
    }
};

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ –æ—Ç–∑—ã–≤–∞
const addReviewSubmissionHandler = (container, productId) => {
    const sendButton = container.querySelector('.send__button');
    const ratingInputs = container.querySelectorAll('.star-rating input[name="rating"]');
    const commentTextarea = container.querySelector('textarea');

    sendButton.addEventListener('click', async () => {

        const selectedRating = Number(Array.from(ratingInputs).find(input => input.checked)?.value); // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤ —á–∏—Å–ª–æ
        const commentText = commentTextarea.value.trim();
        const reviewData = {
            product_id: productId,
            name: '–ì–æ—Å—Ç—å', // –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é
            rating: selectedRating,
            text: commentText,
        };

        // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω–æ—Å—Ç—å
        if (!reviewData.rating || !reviewData.text) {
            alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è');
            return;
        }

        try {
            // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –æ—Ç–∑—ã–≤
            await sendReview(reviewData);

            // –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–∏ –æ—á–∏—â–∞–µ–º —Ñ–æ—Ä–º—É
            ratingInputs.forEach(input => (input.checked = false));
            commentTextarea.value = '';

            // –í—ã–≤–æ–¥–∏–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ
            alert('–°–ø–∞—Å–∏–±–æ, –≤–∞—à –æ—Ç–∑—ã–≤ —Å–æ—Ö—Ä–∞–Ω–µ–Ω.');

            // –ü–µ—Ä–µ—Ä–∏—Å–æ–≤—ã–≤–∞–µ–º —Å–µ–∫—Ü–∏—é –æ—Ç–∑—ã–≤–æ–≤
            await renderReviewsSection(productId);
        } catch (error) {
            alert('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –æ—Ç–∑—ã–≤–∞.');
        }
    });
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –ø–æ–ª–Ω–æ–π —Å–µ–∫—Ü–∏–∏ –æ—Ç–∑—ã–≤–æ–≤
const createReviewsSectionComponent = (reviews, productId, productTitle) => {
    const container = document.createElement('div');
    container.className = 'reviews-section';

    container.innerHTML = `
        <div class="leave-review">
            <h5>–û—Å—Ç–∞–≤–∏—Ç—å –æ—Ç–∑—ã–≤</h5>
            <ul>
                <li>
                    <label>–í–∞—à–∞ –æ—Ü–µ–Ω–∫–∞</label>
                    <div class="star-rating">
                        <input type="radio" id="star5" name="rating" value="5">
                        <label for="star5" title="–û—Ç–ª–∏—á–Ω–æ">‚òÖ</label>
                        
                        <input type="radio" id="star4" name="rating" value="4">
                        <label for="star4" title="–•–æ—Ä–æ—à–æ">‚òÖ</label>
                        
                        <input type="radio" id="star3" name="rating" value="3">
                        <label for="star3" title="–ù–µ–ø–ª–æ—Ö–æ">‚òÖ</label>
                        
                        <input type="radio" id="star2" name="rating" value="2">
                        <label for="star2" title="–ü–ª–æ—Ö–æ">‚òÖ</label>
                        
                        <input type="radio" id="star1" name="rating" value="1">
                        <label for="star1" title="–£–∂–∞—Å–Ω–æ">‚òÖ</label>
                    </div>
                </li>
                <li>
                    <label>–í–∞—à –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π</label>
                    <textarea placeholder="–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç..."></textarea>
                </li>
                <li>
                    <button class="send__button">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                </li>
            </ul>
        </div>
    `;

    const reviewsListComponent = createReviewsListComponent(reviews, productTitle);
    container.prepend(reviewsListComponent);


    addReviewSubmissionHandler(container, productId);

    return container;
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ —Å–µ–∫—Ü–∏–∏ –æ—Ç–∑—ã–≤–æ–≤
const renderReviewsSection = async (productId) => {
    console.log('–§—É–Ω–∫—Ü–∏—è renderReviewsSection –≤—ã–∑–≤–∞–Ω–∞ —Å ID:', productId);
    try {
        const reviews = await getReviewsByProductId(productId);
        const product = await getProductById(productId);
        const productTitle = product.title; 

        const placeholder = document.querySelector('.reviews-placeholder');
        if (!placeholder) {
            console.error('–ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –æ—Ç–∑—ã–≤–æ–≤ –Ω–µ –Ω–∞–π–¥–µ–Ω');
            return;
        }

        const reviewsSectionComponent = createReviewsSectionComponent(reviews || [], productId, productTitle);
        placeholder.innerHTML = ''; 
        placeholder.appendChild(reviewsSectionComponent);
    } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–µ –æ—Ç–∑—ã–≤–æ–≤:', error);
    }
};

export { renderReviewsSection };
